import { HttpErrorResponse, HttpStatusCode } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { StudySessionView } from 'src/app/Dtos/studySessionView';
import { StudySessionService } from 'src/app/services/study-session.service';

@Component({
  selector: 'app-study-session-entry',
  templateUrl: './study-session-entry.component.html'
})
export class StudySessionEntryComponent implements OnInit {
  sessionForm!: FormGroup;
  errorMessage: string = "";

  constructor(private sessionService: StudySessionService,
    private router: Router,
    private formBuilder: FormBuilder) {}

  ngOnInit(): void {
    this.sessionForm = this.formBuilder.group({
      studySessionId: [0],
      topic: [""],
      links: this.formBuilder.array([]),
      tags: this.formBuilder.array([]),
      minutesStudied: [0],
      notes: ["", Validators.required],
      dateStudied: [new Date()]
    })
  }

  submit(): void {

    if (this.sessionForm?.invalid) {
      this.errorMessage = 'Form invalid';
      return;
    }

    const newSession: StudySessionView = this.sessionForm?.value;

    this.sessionService.saveSession(newSession).subscribe({
      next: res => {
        console.log('Session saved.');
      },
      error: (error: HttpErrorResponse) => {
        console.error(error);
        if (error.status === HttpStatusCode.BadRequest) {
          this.errorMessage = 'There was a problem submitting your request. Check your entries and try again.'
        } else if (error.status === HttpStatusCode.Unauthorized) {
          this.errorMessage = "You are unauthorized. Try logging in."
        } else {
          this.errorMessage = "There was an error. The system must be having a bad day. Try again later."
        }
      }
    });
  }

}
