import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs';

import { AppConfigService } from '../app-config-service.service';
import { StudySessionView } from '../Dtos/studySessionView';

@Injectable({
  providedIn: 'root'
})
export class StudySessionService {
  
  controller: string = "StudySession"

  get token(): string | null {
    return localStorage.getItem('token');
  }

  requestOptions = {
    headers: new HttpHeaders({
      'Authorization': `Bearer ${this.token}`
    })
  }

  constructor(private httpClient: HttpClient,
    private appConfigService: AppConfigService) { }

  getAllSessions(): Observable<StudySessionView[]> {
    return this.httpClient.get<StudySessionView[]>(`${this.appConfigService.baseUrl}${this.controller}`, this.requestOptions);
  }

  getRecentSessions(): Observable<StudySessionView[]> {
    return this.httpClient.get<StudySessionView[]>(`${this.appConfigService.baseUrl}${this.controller}/Recent`, this.requestOptions );
  }

  saveSession(sessionToSave: StudySessionView): Observable<void> {
    return this.httpClient.post<void>(`${this.appConfigService.baseUrl}${this.controller}`, sessionToSave, this.requestOptions);
  }

}
